/* Hamburger icon container */
.hamburger {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 24px;
  transform: scale(1.1);
  transition: transform 0.3s ease; /* Smooth rotation transition */
}

/* Hamburger lines */
.hamburger .line {
  background-color: #fff;
  height: 4px;
  width: 100%;
  transition: transform 0.3s ease; /* Smooth line transition */
}

/* Open state: Rotate the hamburger icon */
.hamburger.open {
  transform: rotate(45deg); /* Rotate the icon when open */
}

/* Open state: Modify lines when hamburger is open */
.hamburger.open .line:nth-child(1) {
  transform: translateY(8px) rotate(45deg); /* Top line */
}



.hamburger.open .line:nth-child(2) {
  opacity: 0; /* Hide the middle line */
}

.hamburger.open .line:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg); /* Bottom line */
}

/* Menu dropd margin-bottom: 12px;own */
.dropdown-menu {
  background-color: #333;
  color: #fff;
  padding: 20px;
  margin-top: 10px;
  transition: transform 0.3s ease-in-out;
  transform: translateX(-100%); /* Initially hidden */
}

.dropdown-menu.open {
  transform: translateX(0); /* Slide-in effect when open */
}

.dropdown-menu.label{
  margin-bottom: 12px;
}
